<template>
  <div class="table">
    <table>
      <tr>
        <td>번호</td>
        <td>제목</td>
        <td>클릭횟수(조회수)</td>
      </tr>

      <tr 
      v-for="board in $store.state.board"
      :key="board.id">
        <td>{{board.id}}</td>
        <td @click="count(board.id)">
            {{board.title}}
        </td>
        <td>{{board.count}}</td>

      </tr>


    </table>
  </div>
</template>

<script>
export default {
  name: 'DataTable',
  methods : {
    count(id) {
      this.$store.commit("count", id);
      this.$router.push(`/board/${id}`);
    }
  }
}
</script>

